
TechsController.$inject = ["$http"];
routesConfig.$inject = ["$stateProvider", "$urlRouterProvider", "$locationProvider"];angular
  .module('app', ['ui.router']);

angular
  .module('app')
  .component('fountainTitle', {
    templateUrl: 'app/layout/title/title.html'
  });

angular
  .module('app')
  .component('app', {
    templateUrl: 'app/layout/main/main.html'
  });

angular
  .module('app')
  .component('fountainHeader', {
    templateUrl: 'app/layout/header/header.html'
  });

angular
  .module('app')
  .component('fountainFooter', {
    templateUrl: 'app/layout/footer/footer.html'
  });

angular
  .module('app')
  .component('fountainTechs', {
    templateUrl: 'app/techs/techs.html',
    controller: TechsController
  });

/** @ngInject */
function TechsController($http) {
  var vm = this;

  $http
    .get('app/techs/techs.json')
    .then(function (response) {
      vm.techs = response.data;
    });
}

angular
  .module('app')
  .component('fountainTech', {
    templateUrl: 'app/techs/tech.html',
    bindings: {
      tech: '<'
    }
  });

angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/techs/tech.html","<div class=\"tech\">\n  <img class=\"tech-logo\" ng-src=\"{{ $ctrl.tech.logo }}\">\n  <h3 class=\"tech-h3\">\n    {{ $ctrl.tech.title }}\n  </h3>\n  <p>{{ $ctrl.tech.text1 }}</p>\n  <p>{{ $ctrl.tech.text2 }}</p>\n</div>\n");
$templateCache.put("app/techs/techs.html","<div class=\"techs-container\">\n  <h2 class=\"techs-h2\">\n    Cooked with all these awesome technologies:\n  </h2>\n  <div class=\"techs\">\n    <fountain-tech ng-repeat=\"tech in $ctrl.techs\" tech=\"tech\"></fountain-tech>\n  </div>\n</div>\n");
$templateCache.put("app/layout/footer/footer.html","<footer class=\"footer\">\n  Build with ♥ by the&nbsp;\n  <a href=\"#\">\n    Frontend on Fly Team\n  </a>\n</footer>\n");
$templateCache.put("app/layout/header/header.html","<header class=\"header\">\n  <p class=\"header-title\">\n    <a href=\"#\" target=\"_blank\">\n      Frontend on fly\n    </a>\n  </p>\n</header>\n");
$templateCache.put("app/layout/main/main.html","<div class=\"main-container\">\n  <fountain-header></fountain-header>\n  <main class=\"main\">\n    <fountain-title></fountain-title>\n    <fountain-techs></fountain-techs>\n  </main>\n  <fountain-footer></fountain-footer>\n</div>\n");
$templateCache.put("app/layout/title/title.html","<div class=\"title\">\n  <h1 class=\"title-h1\">\'Allo, \'Allo!</h1>\n  <div>\n    <img class=\"title-logo\" src=\"http://fountainjs.io/assets/imgs/yeoman.png\">\n    <img class=\"title-logo\" src=\"http://fountainjs.io/assets/imgs/fountain.png\">\n  </div>\n  <h2 class=\"title-h2\">Always a pleasure scaffolding your apps</h2>\n</div>\n");}]);
angular
  .module('app')
  .config(routesConfig);

/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(true).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('app', {
      url: '/',
      component: 'app'
    });
}
